@import url(https://fonts.googleapis.com/css?family=Orbitron);
body {
  -webkit-user-select: none;
  -ms-user-select: none;
  -moz-user-select: none;
       user-select: none;
  background-color: #9BBDF9;
  color: #240623;
  font-family: orbitron;
}

#root {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

input:focus {
  outline: none;
}

#drum-machine {
  display: flex;
}

#displayContainer {
  display: flex;
  justify-content: center;
  margin-top: 30px;
  margin-bottom: 30px;
  align-items: center;
  background-color: rgb(202, 202, 202);
  height: 40px;
  width: 200px;
  border-radius: 5px;
  font-size: 10px;
}

#displayContainer h1 {
  text-align: center;
}

#drum-machine {
  width: 555px;
  background-color: #B95F89;
  border: 5px solid #B8336A;
  border-radius: 10px;
}

@media screen and (max-width: 650px) {
  #drum-machine {
    flex-direction: column;
    width: 350px;
    justify-content: center;
  }
  #volumeContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  #padBank {
    margin-top: 10px;
    display: flex;
    justify-content: center;
  }
  #displayContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px;
    margin-left: 21%;
  }
}
.drum-pad {
  display: flex;
  width: 100px;
  height: 80px;
  margin: 5px 5px;
  justify-content: center;
  align-items: center;
  background-color: #c2c2c2;
  border-radius: 10%;
  border-bottom: 2px solid black;
  font-size: 30px;
}

.drum-pad:hover {
  cursor: pointer;
}
#padBank {
  display: flex;
  flex-wrap: wrap;
  width: 350px;
}

#padBank .drum-pad {
  font-weight: bold;
}
#volumeContainer p {
  margin-top: 0;
  font-size: 20px;
  text-align: center;
}

#volumeContainer #volRange {
  width: 198px;
}
#toggleContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#toggleContainer .bankSwitch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  margin-top: -15px;
}

#toggleContainer .bankSwitch input {
  opacity: 0;
  width: 0;
  height: 0;
}

#toggleContainer .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #CCC;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px;
}

#toggleContainer .slider:before {
  position: absolute;
  content: '';
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}

#toggleContainer input:checked + .slider {
  background-color: #2196F3;
}

#toggleContainer input:focus + .slider {
  box-shadow: 0 0 #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}
