#toggleContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
}

#toggleContainer .bankSwitch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  margin-top: -15px;
}

#toggleContainer .bankSwitch input {
  opacity: 0;
  width: 0;
  height: 0;
}

#toggleContainer .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #CCC;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px;
}

#toggleContainer .slider:before {
  position: absolute;
  content: '';
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}

#toggleContainer input:checked + .slider {
  background-color: #2196F3;
}

#toggleContainer input:focus + .slider {
  box-shadow: 0 0 #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}